.container {
  padding: 2% 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lists {
  display: flex;
  justify-content: flex-start;
  padding: 50px 0;
  width: fit-content;
  max-width: 95%;
  min-height: 200px;
  overflow: scroll;
  overflow-y: hidden;
  &::-webkit-scrollbar {
    display: none;
  }
  scrollbar-width: none;
}

